  const drawWheel = (ctx, width, height, angleOffset = 0) => {
    const radius = width / 2
    const centerX = width / 2
    const centerY = height / 2
    const arc = (2 * Math.PI) / prizes.length

    ctx.clearRect(0, 0, width, height)

    prizes.forEach((prize, i) => {
      const startAngle = i * arc + angleOffset - Math.PI / 2
      const endAngle = startAngle + arc
      // console.log(startAngle, endAngle)
      // 顏色區塊
      ctx.beginPath()
      ctx.moveTo(centerX, centerY)
      ctx.arc(centerX, centerY, radius, startAngle, endAngle)
      ctx.closePath()
      ctx.fillStyle = colorRef.current[i]
      ctx.fill()

      // 文字
      ctx.save()
      ctx.translate(centerX, centerY)
      ctx.rotate(startAngle + arc / 2)
      ctx.textAlign = 'right'
      ctx.fillStyle = '#fff'
      ctx.font = '14px sans-serif'
      ctx.fillText(prize, radius - 10, 5)
      ctx.restore()
    })

    // 中央指針
    ctx.beginPath()
    ctx.moveTo(centerX, centerY - radius + 10)
    ctx.lineTo(centerX - 10, centerY - radius - 20)
    ctx.lineTo(centerX + 10, centerY - radius - 20)
    ctx.closePath()
    ctx.fillStyle = 'black'
    ctx.fill()
  }

  useEffect(() => {
    const canvas = canvasRef.current
    const ctx = canvas.getContext('2d')
    drawWheel(ctx, canvas.width, canvas.height, angle)
  }, [angle, prizes])

  const startSpinning = () => {
    if (isSpinning) return
    setIsSpinning(true)

    let currentAngle = angle
    const totalRotation = Math.random() * 360 + 360 * 6 // 至少轉 6 圈
    const target = currentAngle + (totalRotation * Math.PI) / 180
    const duration = 3000
    const startTime = performance.now()

    const animate = (now) => {
      const elapsed = now - startTime
      const t = Math.min(elapsed / duration, 1) // 0 ~ 1
      const eased = 1 - Math.pow(1 - t, 3) // ease out cubic
      currentAngle = angle + eased * (target - angle)

      setAngle(currentAngle)
      if (t < 1) {
        requestAnimationFrame(animate)
      } else {
        setIsSpinning(false)
        const degree = 360 / prizes.length
        const deg = ((currentAngle * 180) / Math.PI) % 360
        const prizeIndex = prizes.length - 1 - Math.floor(deg / degree)
        setSelectedPrize(prizes[prizeIndex])
      }
    }

    requestAnimationFrame(animate)
  }

  // console.log(angle)

